// components/Navbar.js
import { useState, useEffect } from "react";
import Link from "next/link";
import Router from "next/router";
import styled from "styled-components";

import MenuIcon from "../components/MenuIcon";

const StyledNav = styled.nav`
  .navbar {
    box-sizing: border-box;
    background-color: ${p => p.theme.lightgreen};
    color: ${p => p.theme.white};
    display: flex;
    flex-wrap: wrap;
    position: relative;
    left: 0;
    top: 0;
    right: 0;
    height: 5rem;
    justify-content: space-between;
    align-items: center;
    z-index: 1000;
    &.active .items {
      left: 0;
    }
  }
  a {
    text-decoration: none;
    color: white;
    &:hover {
      text-decoration: none;
    }
  }
  .site-name {
    font-family: Merriweather;
    font-size: 1.7rem;
    width: 100%;
    text-align: center;
  }
  .item {
    .link {
      width: 100%;
      display: inline-block;
      font-size: 1.5rem;
      background-color: ${p => p.theme.red};
      text-align: center;
      white-space: nowrap;
      padding: 1.5rem 0;
      &:hover {
        cursor: pointer;
        background-color: ${p => p.theme.lightred};
      }
    }
  }
  .items {
    width: 100%;
    order: 2;
    position: absolute;
    top: 5rem;
    left: -100vw;
    transition: left 400ms ease-in-out;
  }
  .toggle {
    padding: 1rem 2rem;
    order: 1;
    position: absolute;
    right: 0;
    z-index: 11;
    button {
      font-size: 1.7rem;
      color: ${p => p.theme.white};
      padding: 0;
      margin: 0;
      background: none;
      border: none;
      outline: none;
      height: 2rem;
      width: 2rem;
      &:active {
        outline: none;
      }
      &:focus {
        outline: none;
      }
      &:hover {
        cursor: pointer;
      }
    }
  }
`;

const NavItem = ({ text, href }) => (
  <li className="item">
    <Link href={href}>
      <a className="item link">{text}</a>
    </Link>
  </li>
);

const Navbar = () => {
  const [menuIsOpen, setMenuIsOpen] = useState(false);
  const toggleMenu = () => setMenuIsOpen(!menuIsOpen);
  useEffect(() => {
    const closeMenu = () => {
      if (menuIsOpen) setMenuIsOpen(false);
    };
    Router.events.on("routeChangeStart", closeMenu);
    return Router.events.off("routeChangeStart", closeMenu);
  }, [menuIsOpen]);
  return (
    <StyledNav>
      <ul className={`navbar${menuIsOpen ? " active" : ""}`}>
        <li className="site-name">
          <span>Pilcher Holidays</span>
        </li>
        <li className="items">
          <ul>
            <NavItem text="Home" href="/" />
            <NavItem text="Articles" href="/" />
            <NavItem text="Podcast" href="/" />
          </ul>
        </li>
        <li className="toggle">
          <button onClick={toggleMenu}>
            <MenuIcon menuIsOpen={menuIsOpen} />
          </button>
        </li>
      </ul>
    </StyledNav>
  );
};

export default Navbar;
